.watch-vimeo-block,
.watch-youtube-block {
	background-color: #000;
	position: relative;
	display: block;
	-webkit-transition: height .2s;
	-o-transition: height .2s;
	transition: height .2s;
	max-width: 1080px;
	margin: 0 auto
}

.watch-vimeo-block:hover,
.watch-youtube-block:hover {
	cursor: pointer
}

.watch-vimeo-block .watch-vid-container,
.watch-youtube-block .watch-vid-container {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%;
	background-size: cover;
	background-position: center
}

.watch-vimeo-block .watch-vid-container:after,
.watch-youtube-block .watch-vid-container:after {
	position: absolute;
	height: 100%;
	width: 100%;
	background-color: rgba(0, 0, 0, 0);
	-webkit-transition: .3s linear;
	-o-transition: .3s linear;
	transition: .3s linear
}

.watch-vimeo-block .watch-vid-container:hover:after,
.watch-youtube-block .watch-vid-container:hover:after {
	position: absolute;
	height: 100%;
	width: 100%;
	background-color: rgba(0, 0, 0, .4)
}

.watch-vimeo-block .watch-vid-container:hover .player-content,
.watch-youtube-block .watch-vid-container:hover .player-content {
	-webkit-transition: .3s all;
	-o-transition: .3s all;
	transition: .3s all
}

.watch-vimeo-block .watch-vid-container:hover .player-content .play-button,
.watch-youtube-block .watch-vid-container:hover .player-content .play-button {
	top: calc(50% - 70px);
	left: calc(50% - 61px);
	padding: 70px 61px;
	-webkit-transition: .3s all;
	-o-transition: .3s all;
	transition: .3s all
}

.watch-vimeo-block .watch-vid-container .player-content,
.watch-youtube-block .watch-vid-container .player-content {
	position: absolute;
	z-index: 1;
	color: #fff;
	text-align: center;
	width: 100%;
	top: 50%;
	left: 0;
	-webkit-transition: .3s linear;
	-o-transition: .3s linear;
	transition: .3s linear;
	-webkit-transform: translateX(0) translateY(-50%);
	-ms-transform: translateX(0) translateY(-50%);
	    transform: translateX(0) translateY(-50%)
}

.watch-vimeo-block .watch-vid-container .player-content .play-button,
.watch-youtube-block .watch-vid-container .player-content .play-button {
	top: calc(50% - 50px);
	position: absolute;
	left: calc(50% - 41px);
	font-family: Plain-Medium;
	-webkit-transition: .3s all;
	-o-transition: .3s all;
	transition: .3s all;
	overflow: hidden;
	background-color: #000;
	font-size: 10px;
	letter-spacing: 1px;
	padding: 50px 41px;
	border-radius: 50em
}

.watch-vimeo-block .watch-vid-container .player-content .play-button:after,
.watch-youtube-block .watch-vid-container .player-content .play-button:after {
	-webkit-box-shadow: 0 7px 54px 0 rgba(20, 20, 20, .3);
	        box-shadow: 0 7px 54px 0 rgba(20, 20, 20, .3);
	content: '';
	background-color: #3300ad;
	display: block;
	height: 100px;
	width: 100px;
	position: absolute;
	top: -24px;
	left: -32px;
	border-radius: 50em;
	z-index: -1
}

.embed-container {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%
}

.embed-container embed,
.embed-container iframe,
.embed-container object {
	position: absolute;
	top: 0;
	height: 100%;
	left: -4px;
	right: -4px;
	width: calc(100% + 8px)
}


